/*Global css file*/
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  background: transparent;
}

.App {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #121212;
}
.HEADER {
  width: 100%;
  margin-top: 0px;
}
.CONTENT {
  width: 100%;
  background: #121212;
}
.CONTATO {
  width: 100%;
  background: #121212;
}
.FOOTER {
  display: flex;
  width: 100%;
  background: #121212;
  justify-content: center;
}
